* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #eee7e7;
  background-color: #27374d;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.js-hidden {
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.breed-select {
  color: #202022;
  background-color: #a0b7e2;
  border: 2px solid #464da8;
  border-radius: 5px;
  padding: 5px;
  font-size: 18px;
}

.breed-select:focus {
  outline: 2px solid #464da8;
}

.cat-info {
  flex-direction: row;
  margin-top: 20px;
  display: flex;
}

.cat-img {
  border-radius: 20px;
  width: 400px;
  height: 100%;
  box-shadow: 1px 1px 18px #464da8;
}

.cat-text {
  border: 2px solid #464da8;
  border-radius: 20px;
  margin-left: 20px;
  padding: 20px;
}

.cat-header:after {
  content: "";
  background-color: #464da8;
  width: 100%;
  height: 2px;
  display: block;
  box-shadow: 1px 1px 18px #464da8;
}

